import styled from 'styled-components';

const PageContainer = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #FFFFFF
  padding-top: 4rem;
  min-height: 100vh;
`;

export default PageContainer;



